@import "../../vars.css";

.container{
    color: var(--color-text);
    margin-top: 79px;
    margin-left: 10%;
    margin-right: 10%;
    background-color: transparent;
    color: var(--color-text-dark);
}

.container.light {
    
    background-color: transparent;
    color: var(--color-text-light);
}

.title{
    color: var(--color-text);
    font-size: 35px;
    font-weight: 700;
    letter-spacing: 1.75px;
    text-transform: uppercase;
}

.content{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: 14px;
}

.skills{
    width: 45%;
    display: flex;
    flex-wrap: wrap;
    margin-top: 200px ;
    gap:25px;
    
}
.skills.light{
    
}

.skill{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 11px;
}

.skillImageContainer{
    background-color: rgb(255, 255, 255);
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 120px;
    height: 120px;
    
}
.skillImageContainer.light{
    background-color: rgb(0, 0, 0);
}

.skillImageContainer:hover {
    transform: translateY(-10px);
}
.title:hover{
    transform: translateY(-10px);
}

.skillImageContainer img{
    width: 75px;
}

.skill p{
    font-size: 25px;
    font-family:var(--font-roboto);
    font-weight: 500;
}

.history{
    width:45%;
    display: flex;
    flex-direction:column;
    gap: 40px;
}

.historyItems{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 17px;
    background: #19376d;
    background: linear-gradient(
        50deg,
        #19376d 0%,
        rgba(25,55,109,0) 100%
    );
    border-radius: 10px;
    padding: 24px;
}

.historyItems img{
    width: 50px;
}

.historyItemsDetails{
    font-family:var(--font-roboto);
}

.historyItemsDetails h3{
    font-size: 30px;
    font-weight: 500;
}

.historyItemsDetails p{
    font-size: 20px;
    font-weight: 300;
}

.historyItemsDetails ul{
    margin-top: 6px;
    list-style-position: inside;
    font-size: 25px;
    list-style-type: disc;
    margin-left: 17px;
}

.historyItems:hover {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    animation-name: example;
    animation-duration: 0.25s;
    border-left: 8px solid rgb(240, 238, 238);
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  }
.historyItems:hover.light {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    animation-name: example;
    animation-duration: 0.25s;
    border-left: 8px solid rgb(0, 0, 0);
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  }

@media screen and (max-width:830px){
    .content{
        flex-direction: column;
        align-items: center;
        gap: 34px;
    }
    .skills{
        width: 100%;
        flex-direction: row;
        justify-content: center;
    }
    .history{
        width: 100%;
        gap: 9px;
    }
}